import navigationCustom from "../../components/navigation-custom/navigation-custom.vue"

import swiperTabHead from "../../components/index/swiper-tab-head.vue";
import noThing from "../../components/common/no-thing.vue";
import loadMore from "../../components/common/load-more.vue";
import topicList from "../../components/news/topic-list.vue";

export default {
	components:{
		navigationCustom,
		swiperTabHead,
		noThing,
		loadMore,
		topicList
	},
	data() {
		return {
			ischange:false,
			swiperheight:500,
			tabIndex:0,
			tabBars:[],
			newslist:[],
			
			config:{
			    title:"我是标题", //title
			    bgcolor:"#ffffff", //背景颜色
			    fontcolor:"black", //文字颜色，默认白色
			    type:1, //type 1，3胶囊 2，4无胶囊模式
			    transparent:false, //是否背景透明 默认白色
			    linear:false, //是为开启下滑渐变
			    share:false, //是否将主页按钮显示为分享按钮
				leftimage:"../../static/icon/navbar/back_.png",
				rightimage:"../../static/icon/navbar/home_.png"
			    // menuIcon:"../static/icon/back_.png", 当type为3或者4的时候左边的icon文件位置，注意位置与当前页面不一样
			    // menuText:"返回", 当type为3或4的时候icon右边的文字
			},
		};
	},
	onLoad(e) {
		if (e.ischange) {
			this.ischange = true;
			// 修改页面标题
			uni.setNavigationBarTitle({
				title:"选择所属话题"
			})
		}
		uni.getSystemInfo({
			success: (res)=> {
				let height=res.windowHeight-uni.upx2px(100)
				this.swiperheight=height;
			}
		});
		this.getNav();
	},
	methods:{
		// 获取文章分类
		async getNav(){
			let [err,res] =await this.$http.get('/topicclass');
			if (!this.util.CheckError(err,res)) return;
			let list = res.data.data.list;
			let arr = [];
			let arr2 = [];
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					name:list[i].classname
				})
				arr2.push({
					loadtext:"上拉加载更多",
					list:[],
					page:1,
					firstload:false
				});
			}
			this.tabBars = arr;
			this.newslist = arr2;
			this.tabBars.length > 0 && this.getList();
		},
		// 获取指定列表
		async getList(){
			let url = `/topicclass/${this.tabBars[this.tabIndex].id}/topic/${this.newslist[this.tabIndex].page}`;
			let [err,res] = await this.$http.get(url);
			let error = this.util.CheckError(err,res,()=>{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			},()=>{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			});
			if (!error) return;
			let arr = [];
			let list = res.data.data.list;
			console.log(list)
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					titlepic:list[i].titlepic,
					title:list[i].title,
					desc:list[i].desc,
					totalnum:list[i].post_count,
					todaynum:list[i].todaypost_count,
				});
			}
			this.newslist[this.tabIndex].list = this.newslist[this.tabIndex].page > 1 ? this.newslist[this.tabIndex].list.concat(arr) : arr;
			this.newslist[this.tabIndex].firstload = true;
			if (list.length < 10) {
				this.newslist[this.tabIndex].loadtext="没有更多数据了";
			}else{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			}
			return;
		},
		loadmore(index){
			if(this.newslist[index].loadtext!="上拉加载更多"){ return; }
			// 修改状态
			this.newslist[index].loadtext="加载中...";
			this.newslist[this.tabIndex].page++;
			this.getList();
		},
		// tabbar点击事件
		tabtap(index){
			this.tabIndex=index;
		},
		// 滑动事件
		tabChange(e){
			this.tabIndex=e.detail.current;
			this.getList();
		},
		clickright(){
			uni.switchTab({
				url:"../index/index"
			})
		},
		clickleft(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
